import React, { Suspense, lazy } from 'react'
import { Switch, Route } from 'react-router-dom'
import { connect } from 'react-redux'
import Loading from '../../../components/light/Loading'
//import Home from './home/Home'

function List(props) {
  const { router } = props
  //const Home = lazy(() => import('./home/Home'))
  const categoryList = lazy(() => import('./baseMgt/categoryMgt/CategoryList'))
  // const Home = lazy(async () => {
  //   return await new Promise((resolve, reject) => {
  //     setTimeout(() => {
  //       resolve(import('./home/Home'))
  //     }, 1000)
  //   })
  // })

  const listRender = (arr) => {
    return arr.map((item) => {
      if (item.children) {
        return listRender(item.children)
      } else {
        return (
          <Route
            key={item.path}
            path={item.path}
            render={() => {
              const path = item.component
              const MyComponent = lazy(() => import(`${path}`))
              return <MyComponent></MyComponent>
            }}
          ></Route>
        )
      }
    })
  }

  const listDom = listRender(router)

  const historyDom = (
    <>
      <Route
        path="/light/index/home"
        render={() => {
          const Home = lazy(() => import('./home/Home'))
          return <Home></Home>
        }}
      ></Route>
      <Route
        path="/light/index/baseMgt/categoryMgt/categoryList"
        component={categoryList}
      ></Route>
    </>
  )

  return (
    <div className="m-list">
      <Suspense fallback={<Loading isLazyLoading={true}></Loading>}>
        <Switch>
          {listDom}
        </Switch>
      </Suspense>
    </div>
  )
}

const mapStateToProps = (state) => {
  console.log(state.toJS())
  return {
    router: state.getIn(['light', 'router']).toJS(),
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    onSetState(key, value) {
      dispatch({ type: 'SET_LIGHT_STATE', key, value })
    },
    onDispatch(action) {
      dispatch(action)
    },
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(List)
