import React from 'react'
import { Menu } from 'antd'
import { connect } from 'react-redux'
import { NavLink, withRouter } from 'react-router-dom'
import Icon from '../../../components/light/Icon'

const { SubMenu } = Menu

function Sidebar(props) {
  const { router, location: {pathname} } = props
  const defaultOpenKeysFn = () => {
    const arr = []
    let myPathname = pathname.replace(/(\/+$)/g, '')
    let arrTemp = myPathname.split('/')
    console.log(arrTemp)
    if(arrTemp.length > 4) {
      for (let i = 4; i < arrTemp.length; i++) {
        arr.push(arrTemp.slice(0, i).join('/'))
      }
    }
    return arr
  }

  const sidebarRender = (arr) => {
    return arr.map((item) => {
      if (item.children) {
        return (
          <SubMenu
            key={item.key}
            title={item.title}
            icon={item.icon && (
              <Icon name={item.icon} className="m-sidebar-icon"></Icon>
            )}
            className={item.className ? item.className : ''}
          >
            {sidebarRender(item.children)}
          </SubMenu>
        )
      } else {
        return (
          <Menu.Item
            key={item.path}
            className={item.className ? item.className : ''}
          >
            {item.icon && (
              <Icon name={item.icon} className="m-sidebar-icon"></Icon>
            )}
            <NavLink to={item.path}>{item.title}</NavLink>
          </Menu.Item>
        )
      }
    })
  }

  const sidebarDom = sidebarRender(router)

  const historyDom = (
    <>
      <Menu.Item key="/light/index/home">
        <Icon name="home" className="m-sidebar-icon"></Icon>
        <NavLink to="/light/index/home">首页</NavLink>
      </Menu.Item>
      <SubMenu key="/light/index/baseMgt" title="基础模块">
        <SubMenu key="/light/index/baseMgt/categoryMgt" title="分类管理">
          <Menu.Item key="/light/index/baseMgt/categoryMgt/categoryList">
            <NavLink to="/light/index/baseMgt/categoryMgt/categoryList">
              分类列表
            </NavLink>
          </Menu.Item>
        </SubMenu>
      </SubMenu>
    </>
  )

  return (
    <div className="m-sidebar">
      <Menu
        defaultSelectedKeys={[pathname]}
        defaultOpenKeys={defaultOpenKeysFn()}
        mode="inline"
        theme="dark"
        inlineCollapsed={false}
      >
        {sidebarDom}
      </Menu>
    </div>
  )
}

const mapStateToProps = (state) => {
  console.log(state.toJS())
  return {
    router: state.getIn(['light', 'router']).toJS(),
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
    onSetState(key, value) {
      dispatch({ type: 'SET_LIGHT_STATE', key, value })
    },
    onDispatch(action) {
      dispatch(action)
    },
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(withRouter(Sidebar))
